*:focus {
    outline: none !important;
}

*::selection {
	background-color: tomato;
	color: hsl(124, 58%, 98%);
}

p {
	font-family: 'Heebo', sans-serif;
}

em {
	text-align: center;
}

a {
	text-decoration: none;
	color: inherit;
}

.emphasis-font {
	font-family: 'Roboto Slab', 'Times New Roman', Times, serif;
}

.download-button {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 50px;
	width: 50px;
	position: fixed;
	bottom: 50px;
	right: 50px;
	padding: 5px;
	border-radius: 100%;
	border: none;
	background-color: rgba(255, 171, 15, 1);
	transition: background-color 0.1s ease-in-out,
				box-shadow 0.1s ease-in-out;
	box-shadow: 0px 2px 3px 1px rgba(0,0,0,0.2),
				0px 3px 4px 4px rgba(0,0,0,0.05);

}

.download-button:hover {
	cursor: pointer;
	background-color: rgb(255, 151, 15);
}


.download-button:focus {
	background-color: rgb(255, 151, 15);
	box-shadow: 0px 2px 3px 1px rgba(0,0,0,0.2),
	0px 3px 4px 4px rgba(0,0,0,0.05);
}

.download-button:active {
	/* background-color: rgb(241, 118, 18); */
	background-color: rgb(255, 99, 71);
	box-shadow: 0px 1px 2px 1px rgba(0,0,0,0.3);
	transform: scale(0.98);
}

.icon-arrow-thick-down-circle .primary {
	fill: rgba(255, 171, 15, 1);
	transition: fill 0.1s ease-in-out;
}

.icon-arrow-thick-down-circle .secondary {
	fill: white;
}
.download-button:hover .icon-arrow-thick-down-circle .primary {
	fill: rgb(255, 151, 15);
}

em::selection {
	background-color: #215C1E;
	color: hsla(41, 100%, 64%, 1);
}
em.time::selection {
	background-color: tomato;
	color: hsl(124, 58%, 98%);
}

.dark-mode .tool::selection {
	background-color: hsl(124, 58%, 98%);
	color: tomato;
}

.dark-mode em::selection {
	background-color: hsla(41, 100%, 64%, 1);
	color: #215C1E;
}
.dark-mode em.time::selection {
	background-color: tomato;
	color: hsl(124, 58%, 98%);
}

.justify-center {
	justify-content: center;
}

.space-between {
	justify-content: space-between;
}

section {
	padding: 1em 0;
}

.m-t-16 {
	margin-top: 16px;
}

.row {
	display: flex;
}


#App {
  background-color: hsl(124, 58%, 98%);
  color: hsl(121, 100%, 11.4%);
  width: 100%;
  transition: background-color 0.2s ease-in-out,
			  color 0.2s ease-in-out;
}

#App .header {
	padding: 20px 50px;
	display: flex;
	justify-content: flex-end;
}

#App .header .dark-mode-button {
	background-color: #215C1E;
	padding: 5px;
	height: 50px;
	width: 50px;
	border-radius: 999px;
	border: 2px solid hsl(124, 58%, 98%);
	transition: background-color ease-in-out 0.2s, 
				box-shadow ease-in-out 0.1s;
}

#App .header .dark-mode-button:hover {
	background-color: #1F361C;
	cursor: pointer;
}

#App .header .dark-mode-button:active {
	transform: scale(0.98);
	background-color: rgb(21, 34, 15);
	box-shadow: 0px 0px 0px 4px rgb(21, 34, 15);
}

#App .header .dark-mode-button:focus {
	background-color: #1F361C;
	border: 2px solid hsl(124, 58%, 98%);
	box-shadow: 0px 0px 0px 4px #1F361C;
}

#App .header .dark-mode-button .moon {
	fill: hsla(41, 100%, 64%, 1);
	transition: fill ease-in-out 0.2s;
	height: 35px;
	width: 35px;
}
#App .header .dark-mode-button:hover .moon {
	fill: rgba(255, 171, 15, 1);
}
#App .header .dark-mode-button:focus .moon {
	fill: rgba(255, 171, 15, 1);
}

#App .description-container {
	width: 100%;	
	display: flex;
	justify-content: center;
}
#App .description {
	width: 50%;	
	font-size: 1.2em;
	line-height: 2em;
}

#App .description-container .description.first:first-child::first-letter {
	font-family: 'Roboto Slab', 'Times New Roman', Times, serif;
	font-size: 3.75em;
	padding: 20px;
	border-top: 4px solid tomato;
	border-left: 4px solid tomato;
	line-height: 1em;
	float: left;
}

#App #skills-container, #App .employment-container {
	display: flex;
	flex-direction: column;
	margin: 2em;
}

#App #skills-container .row h4 {
	padding-left: 0.55em;
	font-weight: 500;
	margin: 0.75em 0;
}
#App .row em {
	background-color: tomato;
	color:hsl(124, 58%, 98%);
	margin-bottom: 1em;
	padding: 0.25em;
	font-family: 'Roboto Slab', 'Times New Roman', Times, serif;
}

#App #skills-container .row .column {
	display: flex;
	flex-direction: column;
	flex-grow: 1;
	width: 33%;
	padding: 0 1em;
	margin: 0 0 1em 0;
}

#App #skills-container .row .column .skill-name-container {
	display: grid;
}

#App #skills-container .row .column .skill-name {
	width: max-content;
	justify-self: center;
	padding: 0;
	font-size: 1.2em;
	line-height: 1.45em;
	border-bottom: 1px solid #215C1E;
	font-weight: 300;
}

#App #skills-container .row .column .skill-name:hover {
	border-bottom: 1px solid transparent;
	background-image: url(/portfolio/assets/squiggle.svg);
	background-repeat: repeat-x;
	background-position-y: 0.90em;
}

#App #skills-container .row .column .tool-container {
	display: flex;
	flex-wrap: wrap;
}

#App #skills-container .row .column .tool-container .tool {
	display: flex;
	justify-content: center;
	align-items: baseline;
	font-family: 'Heebo', sans-serif;
	padding: 5px 15px;
	color: hsl(124, 58%, 98%);
	background-color: rgb(37, 124, 33);
	border-radius: 999px;
	margin: 0.5rem;
	transition: ease-in-out background-color 0.2s;
	font-weight: 600;
}

#App #skills-container .row .column .tool-container .tool:hover {
	background-color: #215C1E;
}

#App .employment-container {
	align-items: center;
}

#App .employment-container .employer {
	width: 50%;
	display: grid;
}

#App .employment-container .employer > p {
	width: max-content;
	font-size: 1.2em;
	border-bottom: 1px solid #215C1E;
	font-weight: 300;
	justify-self: center;
}

#App .employment-container .employer > p:hover {
	border-bottom: 1px solid transparent;
	background-image: url(/portfolio/assets/squiggle.svg);
	background-repeat: repeat-x;
	background-position-y: 0.90em;
}

#App .employment-container .employer .row {
	align-items: baseline;
}

#App .employment-container .employer .row .title {
	font-size: 1.1em;
	font-family: 'Roboto Slab', 'Times New Roman', Times, serif;
}

#App .employment-container .employer .row .time {
	font-family: 'Heebo', sans-serif;
	color: #215C1E;
	background: none;
	padding: 0;
	margin: 0;
}

#App .employment-container .employer ul {
	margin-bottom: 2.25em;
}

#App .employment-container .employer ul li {
	margin: 0.25em 0;
}

#App .project {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 15px;
	width: 50%;
}

#App .project .project-image {
	object-fit: cover;
	height: 200px;
	width: 200px;
	border-radius: 15px;
	box-shadow: 0px 2px 3px 1px hsla(0, 0%, 0%, 0.2);
}

#App .project .title {
	width: max-content;
	font-size: 1.2em;
	border-bottom: 1px solid #215C1E;
	font-weight: 300;
	justify-self: center;
}

#App .project .project-tool {
	font-family: 'Roboto Slab', 'Times New Roman', Times, serif;
}

#App .project .title:hover {
	border-bottom: 1px solid transparent;
	background-image: url(/portfolio/assets/squiggle.svg);
	background-repeat: repeat-x;
	background-position-y: 0.80em;
}

#App.dark-mode {
	background-color: hsl(121, 100%, 11.4%) !important;
	color: hsl(124, 58%, 98%) !important;
}

#App.dark-mode .header .dark-mode-button {
	background-color: hsla(41, 100%, 64%, 1);
	padding: 5px;
	border-radius: 999px;
}

#App.dark-mode .header .dark-mode-button:hover {
	background-color: rgba(255, 171, 15, 1);
}

#App.dark-mode .header .dark-mode-button:active {
	transform: scale(0.98);
	background-color: rgb(223, 145, 2);
	box-shadow: 0px 0px 0px 4px rgb(223, 145, 2);
}

#App.dark-mode .header .dark-mode-button:focus {
	background-color: rgba(255, 171, 15, 1);
	border: 2px solid hsl(124, 58%, 98%);
	box-shadow: 0px 0px 0px 4px rgba(255, 171, 15, 1);
}

#App.dark-mode .header .dark-mode-button .moon {
	fill: whitesmoke;
}

#App.dark-mode .employment-container .employer > p {
	border-bottom: 1px solid hsl(124, 58%, 98%);
}

#App.dark-mode .employment-container .employer > p:hover {
	border-bottom: 1px solid transparent;
	background-image: url(/portfolio/assets/squiggle-dark-mode.svg);
}

#App.dark-mode .employment-container .employer .row em.time {
	color: hsl(120, 12%, 61%);
}

#App.dark-mode #skills-container .row .column .tool-container .tool {
	color: hsl(124, 58%, 98%);
	background-color: rgba(255, 171, 15, 1);
}
#App.dark-mode #skills-container .row .column .tool-container .tool:hover {
	background-color: rgb(255, 131, 15);
}

#App.dark-mode #skills-container .row .column .skill-name {
	border-bottom: 1px solid hsl(124, 58%, 98%);
}

#App.dark-mode #skills-container .row .column .skill-name:hover {
	border-bottom: 1px solid transparent;
	background-image: url(/portfolio/assets/squiggle-dark-mode.svg);
}

#App.dark-mode .project .title {
	border-bottom: 1px solid hsl(124, 58%, 98%);
}

#App.dark-mode .project .title:hover {
	border-bottom: 1px solid transparent;
	background-image: url(/portfolio/assets/squiggle-dark-mode.svg);
}

#App .footer {
	padding: 1.5em;
	display: flex;
	justify-content: flex-end;
}

#App .footer .block {
	height: 4em;
	width: 4em;
	margin-right: 25%;
	border-bottom: 4px solid tomato;
	border-right: 4px solid tomato;
}

#App .description.p-b-0 {
	margin-bottom: 0;
	padding-bottom: 0;
	text-align: center;
}

@media screen and (max-width: 767px) {
	#App .header {
		padding: 20px 30px;
	}
	
	#App .description-container .description {
		width: 100%;	
		padding: 1em;
		font-size: 1.2em;
		line-height: 2em;
	}
	
	#App .employment-container {
		margin: 1em;
	}
	
	#App .employment-container > .row {
		width: 100%;
	}
	#App .employment-container > .row > em {
		width: 100%;
	}
	
	#App .employment-container .employer {
		width: 100%;
	}
	
	#App .employment-container .employer .row {
		flex-direction: column;
	}
	
	#App #skills-container {
		margin: 2em 1em;
	}
	
	#App #skills-container .row {
		flex-direction: column;
	}
	
	#App #skills-container .row .column {
		width: 100%;
		padding: 0;
	}
	
	#App .project {
		width: 100%;
	}
	
	#App .footer .block {
		height: 3em;
		width: 3em;
		margin-right: 0;
	}
	
	.download-button {
		bottom: 30px;
		right: 30px;
	}
}

@media print {
	* {
		background-color: white;
	}
}
